html {
  box-sizing: border-box; }

:root {
  --zero:0;
  --length:0; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%; }

body {
  font-family: "Kumbh Sans", sans-serif;
  background: linear-gradient(180deg, #b068e9 0%, #6463e7 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

.faq {
  max-width: 920px;
  width: 100%;
  min-height: 509px;
  height: auto;
  margin: 0 auto;
  background: #ffffff;
  box-shadow: 0px 50px 50px -20px rgba(53, 18, 122, 0.497159);
  border-radius: 23px;
  margin-top: 129px;
  position: relative; }
  .faq-container {
    padding-right: 95px;
    padding-top: 65px;
    display: flex; }
  .faq-decorate {
    width: auto;
    position: absolute;
    left: -96px;
    top: -4px; }
    .faq-decorate img:last-child {
      display: none; }
    .faq-decorate__image {
      width: 100%;
      height: 100%;
      -o-object-fit: contain;
         object-fit: contain;
      -o-object-position: center;
         object-position: center; }
  .faq-sidebox {
    max-width: 350px;
    width: 100%;
    margin-left: auto; }
  .faq-title {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 32px;
    color: #1e1f36;
    margin-bottom: 35px; }
  .faq-accordeons__item {
    cursor: pointer;
    padding-bottom: 18px;
    margin-bottom: 16px;
    border-bottom: 1px solid #e8e8ea; }
    .faq-accordeons__item:hover p:first-child {
      color: #f47b56; }
  .faq-accordeons__item-question {
    pointer-events: none;
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 14px;
    line-height: 17px;
    color: #4b4c5f;
    transition: 0.3s all; }
    .faq-accordeons__item-question.active {
      color: #1e1f36;
      font-weight: bold; }
      .faq-accordeons__item-question.active span {
        transform: rotateZ(180deg); }
    .faq-accordeons__item-question span {
      perspective: 1000px;
      transition: 0.3s all; }
  .faq-accordeons__item-answer {
    pointer-events: none;
    font-size: 12px;
    line-height: 18px;
    color: #787887;
    max-height: var(--zero);
    overflow: hidden;
    transition: all 0.4s ease-out; }
    .faq-accordeons__item-answer.active {
      margin-top: 11px;
      max-height: var(--length); }

@media screen and (max-width: 1030px) {
  main {
    padding: 0 20px; } }

@media screen and (max-width: 875px) {
  .faq-decorate {
    transform: translateX(-50%);
    left: calc(50% - 15px);
    top: -95px; }
    .faq-decorate img:first-child {
      display: none; }
    .faq-decorate img:last-child {
      display: block;
      width: 237px; }
  .faq-container {
    padding-top: 100px;
    padding-right: 0; }
  .faq-sidebox {
    margin: auto; }
  .faq-title {
    text-align: center; } }

@media screen and (max-width: 500px) {
  .faq-container {
    padding: 100px 24px 48px; }
  .faq-accordeons__item-question {
    font-size: 13px; } }
